$(".fg-label input").each(function (index, el){
    setInputLabelPosition(el)
});

$(".fg-label input").change(function () {
    setInputLabelPosition(this)
})


function setInputLabelPosition(el) {
    let val = $(el).val()

    if(val !== ""){
        $(el).addClass("input-filled")
    } else{
        $(el).removeClass("input-filled")
    }
}
