$(".catalog-detail__tab").click(function () {
    let id = $(this).attr("data-target")
    $(".catalog-detail__tab").removeClass("_active")
    $(this).addClass("_active")

    $(`.tab`).removeClass("_active")
    $(`.tab-${id}`).addClass("_active")
})


$(".faq-card").click(function () {
    $(this).toggleClass("_active")
    $(this).find(".faq-card__hidden").slideToggle()
})

$(".catalog-detail__mob-toggle").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".catalog-detail__mob-content").slideToggle()
})
