$(".order__tab").click(function () {
    let id = $(this).attr("data-target")

    if(id === "pay"){
        $(".order").addClass("_show-pay")
    } else{
        $(".order").removeClass("_show-pay")
    }

})

$("._goToOrder").click(function () {
    $(".order").removeClass("_show-pay")
})

$("._goToPay").click(function () {
    $(".order").addClass("_show-pay")
})

cartCalc()

function cartCalc(){
    $('.cartcalc .ccalc-minus').click(function(){
        let a = $(this).closest('.cartcalc').find('input').val();
        if(a>1){
            let b = +a-1;
            $(this).closest('.cartcalc').find('input').val(b);
        }else{
            $(this).closest('.cartcalc').find('input').val(a);
        }
    });
    $('.cartcalc .ccalc-plus').click(function(){
        let a = $(this).closest('.cartcalc').find('input').val();
        let b = +a+1;
        $(this).closest('.cartcalc').find('input').val(b);
    });
}


$(".cart-card2__calc button").click(function () {
    let val = +$(this).siblings("input").val()
    let cost = +$(this).parents(".cart-card2").find(".cart-card2__price1 .cart-card2__price-num").html().replace(/\s+/g, '').trim()
    $(this).parents(".cart-card2").find(".cart-card2__price2 .cart-card2__price-num").html((val*cost).toLocaleString())

    calcOrderSum()
})


function calcOrderSum() {
    let sum = 0
    $(".cart-card2__price2 .cart-card2__price-num").each(function (index, el){
        sum = sum + +$(el).text().replace(/\s+/g, '').trim()
    });

    $(".cart__sum-num").html(sum.toLocaleString())
}
calcOrderSum()
